import style from "./table.module.scss";

export default function Table() {
  const data = [
    {
      name: " ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: " ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: "   ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: "  ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: " ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: "  ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: "  ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
    {
      name: "    ",
      min: " 50 000 ",
      medium: "10 000 ",
      type: "",
    },
  ];
  return (
    <>
      <table className={style.table}>
        <thead className={style.thead}>
          <tr>
            <th className={style.th1}> </th>
            <th className={style.th2}>  </th>
            <th className={style.th3}> </th>
            <th className={style.th4}> </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className={style.td1}>{row.name}</td>
              <td className={style.td2}>{row.min}</td>
              <td className={style.td3}>{row.medium}</td>
              <td className={style.td4}>{row.type}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
}
